@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
}

body {
  background-color: black;
}

.box {
  background-color: white;
  height: 300px;
  width: 700px;
  margin: auto;
  margin-top: 10px;
}
.box__top {
  display: flex;
  padding: 15px 15px 0 15px;
}
.box__top-title {
  font-size: 18px;
  font-weight: 700;
  line-height: initial;
  color: #236c62;
  margin-top: auto;
}
.box__bottom {
  display: flex;
  margin-top: 18px;
}
.box__bottom--left {
  margin-left: 18px;
}
.box__bottom--right {
  display: flex;
  flex-direction: column;
  margin-right: 20px;
  padding-top: 20px;
  padding-left: 15px;
  padding-right: 10px;
  height: 205px;
  position: relative;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  background: #fff;
  box-shadow: 0px 0px 7px 0px rgba(136, 136, 136, 0.25);
}
.box__bottom--left {
  width: 304px;
  height: 171px;
  background: #e5e5e5;
}
.box__bottom--left p {
  margin-top: 22px;
  font-size: 10px;
  font-weight: 400;
  line-height: normal;
  color: #656565;
}
.box__bottom-video {
  width: 304px;
  height: 171px;
}
.box__bottom-video img {
  height: 100%;
  width: 100%;
  object-fit: contain;
}
.box__info {
  display: flex;
}
.box__info--left {
  position: relative;
  margin-right: 15px;
}
.box__info--left img {
  object-fit: contain;
  height: 123px;
  width: 123px;
}
.box__info--right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.box__info--right img {
  width: 82px;
  height: 30px;
}
.box__info-title {
  font-size: 14px;
  font-weight: bold;
  line-height: 18.2px;
  color: initial;
}
.box__info-desc {
  font-size: 13px;
  font-weight: 400;
  line-height: 15.6px;
  color: initial;
}
.box__info-button {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 15px;
  width: 110px;
  height: 23px;
}
.box__info-button button {
  border: none;
  height: 100%;
  width: 100%;
  border-radius: 10px;
  background: var(--dr-green-2, radial-gradient(259.48% 104.13% at 45.83% 54.55%, rgba(11, 24, 27, 0.5) 0%, rgba(24, 121, 104, 0.5) 51.28%, rgba(0, 200, 164, 0.5) 100%), #20c8a7);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  font-size: 10px;
  font-weight: 800;
  line-height: 12px;
  color: #fff;
}

.swiper-wrapper {
  max-width: 300px;
  display: flex;
  flex-direction: row;
}